import React from "react";
import axios from "axios";
import SingleChoice from "./qz_single_choice";
import MultipleChoice from "./qz_multiple_choice";
import PracticeTest from "./qz_practice_test";
import InstructionsOne from "./gusture_itstruction1"
import InstructionsTwo from "./gusture_itstruction2"
import Header from "./header"
import Grid from "./grid_type"
import SubmitPopup from "./submit_popup"
import GeneralInstructions from "./general_instructions"
import {
  SwipeableList,
  SwipeableListItem
} from '@sandstreamdev/react-swipeable-list';
import { connect } from "react-redux";
import {
  addData1,
  addData2,
  removeData1,
  removeData2,
  updateData1
} from '../store/actions'

const quiz_json = require("../temp/quiz");
const commonservice = require('./services/service')
const threshold = 0.1;
var counts = 0
class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      // transitionEffect:false,
      _instruction_button_: 'Start Test',
      _quiz_json: "",
      _loader: <div style={{ textAlign: "center", marginTop: " 50%" }}>Loading...</div>,
      _generalInstruction: false,
      _showQuestions: false,
      _gestureInstructionOne: false,
      _gestureInstructionTwo: false,
      _submitPopupOpen: false,
      _quiz_id_key: '',
      _questions: [],
      _timer: ''
    };
  }

  getdb(key) {
    return new Promise(async (resolve, reject) => {
      try {
        var dataValue = await window.ldb.get(key);
        resolve(dataValue)
      } catch (e) {
        console.log(e);
      }
    })

  }
  setdb = (key, value) => {
    window.ldb.set(key, JSON.stringify(value));

  }
  onBackButtonEvent = (e) => {
    e.preventDefault();
    window.history.pushState(null, null, window.location.pathname);
    this.setState({ _grid: false })
  }
  async componentDidMount() {
    // console.log(quiz_json.quiz_json)
    window.history.pushState(null, null, window.location.pathname);
    window.addEventListener('popstate', this.onBackButtonEvent);
    // let qz2= quiz_json.quiz_json[Object.keys(quiz_json.quiz_json)[0]]
    // console.log(qz2.testObject)
    // await this.arrangeQuestions(qz2.testObject)
    // this.setState({ _loader: "", _generalInstruction: true });
    let json = JSON.parse(await this.getdb("data"))
    if (localStorage.getItem("quiz_id") == null && json == null) {
      await this.callApi()
    } else {
      let quiz_id_key = localStorage.getItem("quiz_id")
      this.setState({
        _quiz_id_key: quiz_id_key
      });
      await this.arrangeQuestions(json)
      this.setState({ _loader: "", _generalInstruction: true });
    }
    console.log(localStorage.getItem('quiz_status'))
    if (localStorage.getItem('quiz_status') == 'started') {
      this.setState({ _instruction_button_: 'Resume Quiz' })
    }
  }

  swipeLeft = (e) => {
    this.setState({ _grid: true })
  }
  swipeRight = (e) => {
    this.setState({ _grid: false })
  }

  async callApi() {
    localStorage.removeItem('time_left')
    let data = {}
    let res = await axios.get('https://qrpkf5fao2.execute-api.ap-south-1.amazonaws.com/dev')
      .then(res => {
        console.log(res)
        // let quzz = res.data.body[Object.keys(res.data.body)[0]]
        let quzz = quiz_json.quiz_json[Object.keys(quiz_json.quiz_json)[0]]
        console.log(quzz)

        console.log(quzz.testObject)
        data = quzz.testObject
        let account_id = "NYR"
        let test_id = "123"
        // let fullData = JSON.stringify(res.data.body)
        localStorage.setItem("quiz_id", account_id + test_id);
        // data = JSON.parse(fullData)
        this.setState({
          _quiz_id_key: account_id + test_id
        });
        data.SC_attended = {}
        data.MC_attended = {}
        data.PT_attended = {}
        this.arrangeQuestions(data)
        this.setdb("data", data)
        this.setState({ _loader: "", _generalInstruction: true });
      })
      .catch(e => {
        this.setState({ _loader: "Network Error. Check Your Internet Connection", _generalInstruction: false });
        // alert(e)
        return
      })

    // let qz2= quiz_json.quiz_json[Object.keys(quiz_json.quiz_json)[0]]
    // console.log(qz2.testObject)
    // this.arrangeQuestions(qz2.testObject)
    // await this.setdb("data",qz2.testObject)
  }
  arrangeQuestions = (shuffledData) => {
    // console.log(shuffledData)
    return new Promise((resolve, reject) => {
      let question_order = shuffledData.questions_order
      let arranged_questions = []
      question_order.map((element, index) => {
        let qn_obj = {
          id: shuffledData.questions[element].id,
          q_type: shuffledData.questions[element].q_type,
          q_positive_mark: shuffledData.questions[element].q_positive_mark,
          q_negative_mark: shuffledData.questions[element].q_negative_mark,
          q_question: shuffledData.questions[element].q_question,
          q_explanation: shuffledData.questions[element].q_explanation,
          q_answer: shuffledData.questions[element].q_answer,
          options: shuffledData.questions[element].options
        }
        if (!shuffledData.questions[element].status) {
          qn_obj.status = { qn: "notvisited", flag: false }
        } else {
          qn_obj.status = shuffledData.questions[element].status
        }
        arranged_questions.push(qn_obj)
      });
      // console.log(arranged_questions)
      // ***************************************************************************************************8
      // this.props.addData1(arranged_questions)
      // ***************************************************************************************************8
      let singleWithAnswer = commonservice.generatePracticeTestAnswer(arranged_questions)//if practice test
      this.props.addData1(singleWithAnswer)
      // ***************************************************************************************************8
      resolve()
    })
  }
  gridClick = (val) => {
    this.setState({ _grid: val
      // ,transitionEffect:true
     })
  }
  // gridSwipe = (val) => {
  //   this.setState({ _grid: val,transitionEffect:false })
  // }
  openQuestions = () => {
    this.setState({ _showQuestions: true, _generalInstruction: false, _gestureInstructionOne: true });
  };
  closeModal = () => {
    this.setState({ _gestureInstructionOne: false });
  }
  gotoGustureOne_ = (e) => {
    this.setState({ _showQuestions: true, _generalInstruction: false, _gestureInstructionOne: true });
  }
  gotoGustureTwo_ = (e) => {
    this.setState({ _showQuestions: true, _generalInstruction: false, _gestureInstructionTwo: true });
  }
  OkGotIt_ = (e) => {
    this.setState({
      _showQuestions: true, _gestureInstructionOne: false, _gestureInstructionTwo: false,
      // _timer: 'normal',
      _timer: 'practice'
    });
    localStorage.setItem('quiz_status', 'started')
  }

  openSubmitPopup = () => {
    this.setState({ _submitPopupOpen: true })
  }

  MultipleChoiceResponse = (qn, ans_id, qn_index, optn_index) => {
    if (counts == 0) {
      console.log("called")
      if (this.props.Data1[0][qn_index - 1].options[optn_index]['class'] == "ques-choice-marked") {
        this.props.Data1[0][qn_index - 1].options[optn_index]['class'] = ""
      } else {
        this.props.Data1[0][qn_index - 1].options[optn_index]['class'] = "ques-choice-marked"
      }
      this.props.Data1[0][qn_index - 1].status.qn = 'answered'
      counts = 1
      let count = 0//to solve 2 times calling fn
      this.props.Data1[0][qn_index - 1].options.map((val) => {
        if (val["class"] == 'ques-choice-marked') {
          count = count + 1
        }
      })
      if (count == 0) {
        this.props.Data1[0][qn_index - 1].status.qn = 'unanswered'
      }
      setTimeout(() => { counts = 0 }, 50)
      commonservice.updateToiDB_multiple_choice(qn, ans_id, optn_index, this.props.Data1[0][qn_index - 1])
    }

  }

  PracticeTestResponse = (qn, ans_id, qn_index, optn_index) => {
    // console.log(qn, ans_id, qn_index, optn_index)
    // qn, ans_id, qn_index, optn_index, a, b, c
    // this.props.Data1[0][qn_index - 1].options[a]['class'] = ""
    // this.props.Data1[0][qn_index - 1].options[b]['class'] = ""
    // this.props.Data1[0][qn_index - 1].options[c]['class'] = ""
    this.props.Data1[0][qn_index - 1].options[optn_index]['class'] = "ques-choice-marked"
    this.props.Data1[0][qn_index - 1].status.qn = 'answered'
    //  console.log(this.props.Data1[0])
    // this.setState({ _questions:  this.props.Data1[0] })
    commonservice.updateToiDB_PracticeTest(qn, ans_id, this.props.Data1[0][qn_index - 1])
  }

  SingleChoiceResponse = (qn, ans_id, qn_index, optn_index, a, b, c) => {
    this.props.Data1[0][qn_index - 1].options[a]['class'] = ""
    this.props.Data1[0][qn_index - 1].options[b]['class'] = ""
    this.props.Data1[0][qn_index - 1].options[c]['class'] = ""
    this.props.Data1[0][qn_index - 1].options[optn_index]['class'] = "ques-choice-marked"
    this.props.Data1[0][qn_index - 1].status.qn = 'answered'
    //  console.log(this.props.Data1[0])
    // this.setState({ _questions:  this.props.Data1[0] })
    commonservice.updateToiDB_single_choice(qn, ans_id, this.props.Data1[0][qn_index - 1])
  }

  FlagResponse = (qn_id, qn_index) => {
    if (this.props.Data1[0][qn_index - 1].status.flag == false) {
      this.props.Data1[0][qn_index - 1].status.flag = true
    } else {
      this.props.Data1[0][qn_index - 1].status.flag = false
    }
    commonservice.updateToiDB_ribbonStatus(qn_id, this.props.Data1[0][qn_index - 1])
  }

  render() {
    return (
      <div>
 {this.state._loader}
        {this.state._generalInstruction && (
          <GeneralInstructions gotoGustureOne={this.gotoGustureOne_} _instruction_button_={this.state._instruction_button_} />
        )}
        {this.state._showQuestions && (
          <div>
            {/* <section class="quiz-topic-header-block">
              <div class="quiz-topic-header">
                JEE 2020 Revision: Thermal Properties of Matter Quiz
              </div>
              <div class="quiz-topic-ques-resp">
                <div class="quiz-total-questions">
                  {this.props.Data1[0].length} Questions
                </div>
                <div class="quiz-total-time">
                  30 Minutes
                </div>
              </div>
            </section> */}
              <Header submit_clicked={this.openSubmitPopup} grid_click={this.gridClick} timer={this.state._timer} />
                <div id="swipe" >
            <SwipeableList threshold={threshold}>
              <SwipeableListItem

                swipeLeft={{
                  // content:  <div> {this.swipeLeft() }</div>,
                  action: (e) => { console.log("swipe");this.swipeLeft() }
                }}
                // swipeRight={{
                //   // content: <div>Revealed content during swipe</div>,
                //   action: () => { this.swipeLeft() }
                // }}
              >
                  {
                    this.props.Data1[0].map((qstn, index) => {
                      // if (qstn.q_type == 1) {
                      //   return (
                      //     <div key={index}>
                      //       <SingleChoice question={qstn} index={index} questions_length={this.props.Data1[0].length} response={this.SingleChoiceResponse} flag_response={this.FlagResponse} />
                      //     </div>
                      //   )
                      // }
                      // if (qstn.q_type == 2) {
                      //   return (
                      //     <div key={index}>
                      //       <MultipleChoice question={qstn} index={index} questions_length={this.props.Data1[0].length} response={this.MultipleChoiceResponse} flag_response={this.FlagResponse} />
                      //     </div>
                      //   )
                      // }
                      // console.log(qstn)
                      return (

                        <PracticeTest question={qstn} index={index} questions_length={this.props.Data1[0].length} response={this.PracticeTestResponse} flag_response={this.FlagResponse} />
                      )
                    })
                  }
              </SwipeableListItem>
            </SwipeableList>
                </div>
          </div>
        )}
        {this.state._submitPopupOpen && (
          <SubmitPopup cancel_click={() => this.setState({ _submitPopupOpen: false })} />
        )}
        {this.state._gestureInstructionOne && (
          <InstructionsOne gotoGustureTwo={this.gotoGustureTwo_} />
        )}
        {this.state._gestureInstructionTwo && (
          <InstructionsTwo OkGotIt={this.OkGotIt_} />
        )}
        {this.state._grid && (
          <Grid  grid_click={this.gridClick} />
        )}
       
      </div>
    );
  }
}


export default connect(
  ({ Data1, Data2 }) => ({ Data1, Data2 }),
  {
    addData1,
    addData2,
    removeData1,
    removeData2,
    updateData1
  }
)(Home)