import React from "react";
const parse = require("html-react-parser");

export default class MultipleChoice extends React.Component {

  state = {
    question: '',
    questions: [],
    index: ''
  };

  componentDidMount() {
    // console.log(this.props.question)
    this.setState({ question: this.props.question })
    this.setState({ questions: this.props.questions })
    this.setState({ index: this.props.index + 1 })
  }

  inputLoad = (val,qn_id) => {
    setTimeout(() => {
      if (document.getElementById(val.id+"-"+qn_id))
        if (document.getElementById(val.id+"-"+qn_id).checked != true)
          if (val['class'] == "ques-choice-marked") {
            document.getElementById(val.id+"-"+qn_id).checked = true
          }
    }, 500)
  }

  changeFlag(id) {
    if (document.getElementById(id).src.match("img/ribbon_marked.svg")) {
      document.getElementById(id).src = "img/ribbon.svg"
    } else {
      document.getElementById(id).src = "img/ribbon_marked.svg"
    }
  }

  render() {
    return (
      <div>
        <section class="practice-session quiz-starting-session">
          <div  id={this.props.question.id} class="quiz-onscroll ps-ques-wrapper" >
            <span class="ribbon" onClick={() => this.props.flag_response(this.props.question.id, this.state.index)}>
              <img id={this.props.question.id + "-flag"} onClick={() =>{this.changeFlag(this.props.question.id + "-flag")}} src={this.props.question.status.flag == true ? "img/ribbon_marked.svg" : "img/ribbon.svg"} />
            </span>
            <div class="ps-ques-header-wrapper">
              <div class="ps-ques-header-container">
                <div class="ps-ques-header">
                  Multiple Choice
                </div>
                <div class="ps-ques-count">
                  <span class="ps-current-ques-no">{this.state.index} /</span>
                  <span class="ps-total-ques">{this.props.questions_length}</span>
                </div>
              </div>
              <div class="ps-ques-points-container">
                <div class="ps-ques-points">+{this.state.question.q_positive_mark}</div>
                <div class="ps-ques-points">-{this.state.question.q_negative_mark}</div>
              </div>
            </div>
            <div class="ps-ques-block ">
              {parse(this.props.question.q_question[1])}
            </div>
            <div class="ps-ques-choice-section" >
              {(this.props.question.options.map((val,i) => {
                return (
                <label key={i} class="ps-choice-container" onClick={() => this.props.response(this.props.question, this.props.question.options[i].id, this.state.index, i)}>
                  <input id={this.props.question.options[i].id+"-"+this.props.question.id } onLoad={this.inputLoad(this.props.question.options[i],this.props.question.id)} type="checkbox" name="choice" />
                  <span class="option">{parse(this.props.question.options[i].qo_options[1])}</span>
                  <span class="choice choice-sel"></span>
                </label>
                )
              }))}
            </div>
          </div>
        </section>
      </div>
    );
  }
}
