import React from "react";
import { connect } from "react-redux";
import {
    addData1,
    addData2,
    removeData1,
    removeData2,
    updateData1
} from '../store/actions'
import { pageVisibilityApi } from "./services/isvisible";
import { Redirect } from "react-router-dom";

const { hidden, visibilityChange } = pageVisibilityApi();

const env = require('./env')


var timeinterval;
var paused = false; // is the clock paused?
var time_left; // time left on the clock when paused
var totalSeconds = 0;
class Header extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            _parcticeTimer: '',
            _timer: '',
            temp_timer: '',
            _asset_url: env.data.asset_url,
            round_ids: [],
            selected_id: '',
            qn_no: 1,
            _timer_val: '',
            deadline: '',
            isVisible: true,
            redirect: null

        };
    }
    f = () => {
        var scrollPos = window.pageYOffset+200
        var containers = document.querySelectorAll('.ques-bubble')
        containers.forEach((val, i) => {
            var currLink = containers[i];
            // console.log(currLink)                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                               
            if (currLink)
                var refElement = document.getElementById(currLink.id.split('#q-')[1])
            if (refElement != null) {
                if (refElement.offsetTop <= scrollPos && refElement.offsetTop + refElement.offsetHeight > scrollPos) {

                    document.querySelector('.ques-bubble').classList.remove("ques-active")

                    currLink.classList.add("ques-active");

                    currLink.scrollIntoView({
                        block: "center",
                        inline: "center",
                        behavior: "auto"
                    });

                }
                else {
                    if (currLink)
                        currLink.classList.remove("ques-active");
                }
            }
        })
    }
    componentDidMount() {
        document.addEventListener(visibilityChange, this.handleVisibilityChange, false);
        document.addEventListener('scroll', this.f)
        // document.querySelector('quiz-onscroll').onscroll=this.f
        let tme = setInterval(() => {
            if (this.props.timer == 'normal') {
                if (JSON.parse(localStorage.getItem('time_left')) != null) {
                    this.resume_clock()

                } else {
                    // var deadline = new Date(current_time + time_in_minutes * 60 * (3000));
                    // this.setState({ deadline: deadline })
                    this.run_clock();
                }
                clearInterval(tme)
            } else if (this.props.timer == 'practice') {
                // console.log("practice")
                this.state._parcticeTimer = setInterval(this.run_practice_clock, 1000);
                clearInterval(tme)
            }
        }, 1000)
    }
    run_practice_clock = () => {

        ++totalSeconds;
        // console.log(this.pad(totalSeconds%60),"ddd",this.pad(parseInt(totalSeconds/60)))
        // secondsLabel.innerHTML = this.pad(totalSeconds%60);
        // minutesLabel.innerHTML = this.pad(parseInt(totalSeconds/60));
        let min = this.pad(parseInt(totalSeconds / 60))
        let sec = this.pad(totalSeconds % 60)
        this.setState({ _timer: min + "m " + sec + "s ", temp_timer: min + ":" + sec })
    }
    pad(val) {
        var valString = val + "";
        if (valString.length < 2) {
            return "0" + valString;
        }
        else {
            return valString;
        }
    }
    componentWillUnmount() {
        if (this.state._parcticeTimer) {
            clearInterval(this.state._parcticeTimer)
        }
        this.pause_clock()
        localStorage.setItem('timeTaken', JSON.stringify({ total: '30:00', taken: this.state.temp_timer }))
    }

    handleVisibilityChange = forcedFlag => {
        if (typeof forcedFlag === "boolean") {
            if (forcedFlag) {
                return this.setVisibility(true);
            }
            return this.setVisibility(false);
        }
        if (document[hidden]) {
            return this.setVisibility(false);
        }
        return this.setVisibility(true);
    };
    forceVisibilityTrue = () => {
        this.handleVisibilityChange(true);
    };

    forceVisibilityFalse = () => {
        this.handleVisibilityChange(false);
    };

    setVisibility = flag => {
        if (flag) {
            this.resume_clock()
        } else {

            this.pause_clock()
        }
    };

    update_clock = () => {
        //  var clock = document.getElementById('clockdiv');
        if (this.state.deadline == '') {
            let time_in_minutes = 30;
            let current_time = Date.parse(new Date());
            this.state.deadline = new Date(current_time + time_in_minutes * 60 * (1000));
            // this.setState({ deadline: deadline })
        }
        var t = this.time_remaining(this.state.deadline);
        // clock.innerHTML =  t.minutes + "m " + t.seconds+ "s ";
        // console.log(t.minutes,t.seconds)
        if (t.minutes == '00' && t.seconds == '01') {
            this.setState({ _timer: "00" + "m " + "00" + "s ", temp_timer: "00" + ":" + "00" })
            clearInterval(this.state._timer_val)
            // document.getElementById('swipe').style.pointerEvents='none'
            this.setState({ redirect: "/submit" });

        } else {
            this.setState({ _timer: t.minutes + "m " + t.seconds + "s ", temp_timer: t.minutes + ":" + t.seconds })
            if (t.total <= 0) { clearInterval(this.state._timer_val); }
        }
    }
    run_clock() {
        // timeinterval = setInterval(this.update_clock, 1000);
        // this.setState({ _timer_val: timeinterval })
    }
    time_remaining(endtime) {
        var t = Date.parse(endtime) - Date.parse(new Date());
        var seconds = Math.floor((t / 1000) % 60);
        var minutes = Math.floor((t / 1000 / 60) % 60);
        var hours = Math.floor((t / (1000 * 60 * 60)) % 24);
        var days = Math.floor(t / (1000 * 60 * 60 * 24));
        if (t < 0) {
            clearInterval(this.state._timer_val)
            return { 'minutes': '00', 'seconds': '01' };
        }
        if (minutes < 10) { minutes = "0" + minutes }
        if (seconds < 10) { seconds = "0" + seconds }
        localStorage.setItem('time_left', t)
        return { 'total': t, 'days': days, 'hours': hours, 'minutes': minutes, 'seconds': seconds };
    }
    pause_clock() {
        // if (!paused) {
        //     paused = true;
        clearInterval(this.state._timer_val); // stop the clock
        // time_left = this.time_remaining(this.state.deadline).total; // preserve remaining time
        // console.log(time_left, "time_left")
        // localStorage.setItem('time_left', time_left)
        // }
    }

    resume_clock = () => {
        // if (paused) {
        //     paused = false;

        // update the deadline to preserve the amount of time remaining
        let left = JSON.parse(localStorage.getItem('time_left'))
        let deadline = new Date(Date.parse(new Date()) + left);
        this.setState({ deadline: deadline })

        // start the clock
        this.run_clock();
        // }
    }
    // componentWillReceiveProps(){
    //     console.log(typeof this.props.qnid,"this.props.qnid")
    //     this.setState({selected_id:toString(this.props.qnid)})
    // }
    gotoRound = (id) => {
        console.log(id,"id")
        console.log(document.querySelector('.ques-bubble'))
        document.querySelector('.ques-bubble').classList.remove("ques-active")
        console.log(document.querySelector('.ques-bubble'))

        let g = document.getElementById('#q-' + id)
        g.classList.add("ques-active");
        g.scrollIntoView({
            block: "center",
            inline: "center",
            behavior: "smooth"
        });
        // document.getElementById(index + 1).scrollIntoView(false)
        
        document.getElementById(id).scrollIntoView(false);
        // this.setState({ selected_id: id, qn_no: index + 1 })
    }
    render() {
        if (this.state.redirect) {
            return <Redirect to={this.state.redirect} />
        }

        return (
            <div class="fixed-header">
                <section class="quiz-topic-header-block">
                    <div class="quiz-topic-header">
                        JEE 2020 Revision: Thermal Properties of Matter Quiz
              </div>
                    <div class="quiz-topic-ques-resp">
                        <div class="quiz-total-questions">
                            {this.props.Data1[0].length} Questions
                </div>
                        <div class="quiz-total-time">
                            30 Minutes
                </div>
                    </div>
                </section>
                <div class="ps-header-column">
                    <div class="ps-header-wrapper">
                        <div class="ps-timer-container">
                            <div class="ps-time-show">
                                Time:
                                </div>
                            <div class="ps-timer">
                                {this.state._timer}
                            </div>
                            <div class="ps-quiz-current-no">
                                {this.state.qn_no}/{this.props.Data1[0].length}
                            </div>
                        </div>
                        <div class="ps-btnblk">
                            <div class="ps-btn" onClick={() => { this.props.submit_clicked() }}>
                                Submit
                                </div>
                        </div>
                    </div>
                </div>
                <section class="practice-session quiz-starting-session" >

                    <div class="ques-select-bubbles-wrapper">
                        <div class="ques-bubbles-container">
                            {this.props.Data1[0].map((val, index) => {
                                return (
                                    <div key={index} onClick={() => { this.gotoRound(val.id) }} id={'#q-' + (val.id)} class={val.status.flag == true ? 'marked ques-bubble' : val.status.qn + ' ques-bubble'}>
                                        {/* class={(this.state.selected_id == val.id) ? 'ques-bubble ques-active' : 'ques-bubble'} */}

                                    </div>
                                )
                            })}
                        </div>
                        <div class="ques-sel-all" onClick={() => this.props.grid_click(true)}>
                            <div class="ques-all-icon">
                                <img src={this.state._asset_url + "6rectangular.svg"} />
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        )
    }
}

export default connect(
    ({ Data1, Data2 }) => ({ Data1, Data2 }),
    {
        addData1,
        addData2,
        removeData1,
        removeData2,
        updateData1
    }
)(Header)