import React from "react";
const parse = require("html-react-parser");
const commonservice = require('./services/service')
export default class solution extends React.Component {

    state = {
        _question: [],
        _question_temp: [],
        index: ''
    };

    async componentDidMount() {
        let props_data = this.props._localJson
        let arranged = await commonservice.generateAnswer(props_data)
        console.log(arranged, "arranged")
        this.setState({ _question: arranged, _question_temp: arranged })
        // document.getElementById('scroltop-result').scrollIntoView(false);
        window.scrollTo({top: 500,behavior: 'smooth'});
    }
   

    Filter = (filter) => {
        console.log(filter)
        if (filter.target.value == 0) {
            this.setState({ _question_temp: this.state._question })
        } else {
            let arr = []
            this.state._question.map((val) => {
                if (val.q_type == filter.target.value) {
                    arr.push(val)
                }
                this.setState({ _question_temp: arr })
            })
        }
    }
    option_logic = (qn) => {

        return qn.options.map((optn, i) => {


            if (optn.class == "ques-choice-marked" && optn.ans == true) {
                if (qn.whole_ans == true) {
                    return (
                        <div key={i} class='test-ques-choice-container  test-ques-correct-answer-section'>
                        <div class="test-ques-full-correct-answer"></div>
                            <div class="test-quesno-choice-counter"></div>
                            <div class="test-ques-choice-options">{parse(optn.qo_options[1])}</div>
                        </div>
                    )
                } else {
                    return (
                        <div key={i} class='test-ques-choice-container  test-ques-correct-answer-section'>
                            <div class="test-ques-correct-answer"></div>
                            <div class="test-quesno-choice-counter"></div>
                            <div class="test-ques-choice-options">{parse(optn.qo_options[1])}</div>
                        </div>
                    )
                }
            } else if (optn['class'] == "ques-choice-marked" && optn.ans == false) {
                return (
                    <div key={i} class='test-ques-choice-container test-quiz-wrong-section'>
                        <div class="test-quiz-wrong-mark"></div>
                        <div class="test-quesno-choice-counter"></div>
                        <div class="test-ques-choice-options">{parse(optn.qo_options[1])}</div>
                    </div>
                )
            } else if (!optn['class'] && optn.ans == true) {
                return (
                    <div key={i} class='test-ques-choice-container test-ques-correct-answer-section'>
                        <div class="test-ques-correct-answer"></div>
                        <div class="test-quesno-choice-counter"></div>
                        <div class="test-ques-choice-options">{parse(optn.qo_options[1])}</div>
                    </div>
                )
            } else {
                return (
                    <div key={i} class='test-ques-choice-container'>
                        {/* <div class="test-ques-correct-answer"></div> */}
                        <div class="test-quesno-choice-counter"></div>
                        <div class="test-ques-choice-options">{parse(optn.qo_options[1])}</div>
                    </div>
                )
            }

        })
    }
    render() {
        return (
            <div>
                <div class="test-show-solution-header">
                    <div class="relative test-show-formblk">
                        <select class="form-control custom-input" name="" onChange={this.Filter}>
                            <option value="0">All Questions</option>
                            <option value="1">Single Choice</option>
                            <option value="2">Multiple Choice</option>
                        </select>
                    </div>
                    <div id="scroltop-result" class="test-total-question">
                        <div>Showing</div>
                        <div>50 of 50 Questions</div>
                    </div>
                </div>
                {
                    this.state._question_temp.map((qn, index) => {
                        // console.log(qn)
                        return (
                            <div key={index} class="test-ques-solution-wrapper">
                                <div class="test-ques-container">
                                    <div class="test-ques-header">
                                        <div class="d-flex align-items-center">
                                            <div class="test-ques-no">{index + 1}.</div>
                                            <div class="test-ques-type">{qn.q_type == 1 ? 'Single Choice' : 'Multiple Choice'}</div>
                                        </div>
                                        {qn.whole_ans == true ? (
                                            <div class="test-ques-unattend" style={{color:'#39b54a'}}>Correct</div>
                                        ) : (
                                             qn.status.qn == 'notvisited' ? (
                                                <div class="test-ques-unattend">Unattended</div>
                                            ):(
                                            <div class="test-ques-unattend" style={{color:'#f44'}}>{qn.status.qn=='unanswered'?'unanswered':'Wrong'}</div>
                                            
                                            )
                                                // <div class="test-ques-unattend">{qn.status.qn == 'notvisited' ? 'Unattended' : 'InCorrect'}</div>
                                            )}

                                    </div>
                                    <div class="test-ques-part">
                                        {parse(qn.q_question[1])}
                                    </div>
                                    <div class="test-ques-choice-wrapper">
                                        {
                                            this.option_logic(qn)
                                        }
                                    </div>
                                </div>
                            </div>
                        )
                    })
                }

            </div>
        );
    }
}
