import React from "react";
const parse = require("html-react-parser");

export default class SingleChoice extends React.Component {

  state = {
    question: '',
    index: ''
  };

  componentDidMount() {
    // console.log(this.props.question)
    this.setState({ question: this.props.question })
    this.setState({ index: this.props.index + 1 })
    // document.getElementById('swipe').addEventListener('swl', this.swipeLeft, false);
    // document.getElementById('swipe').addEventListener('swr', this.swipeRight, false);
  }
  inputClick = (id) => {
    document.getElementById(id).checked = true
  }
  inputLoad = (val,id) => {
    setTimeout(() => {
      if (document.getElementById(id))
      if (document.getElementById(id).checked != true)
      if (val['class'] == "ques-choice-marked") {
        document.getElementById(id).checked = true
          }
    }, 500)
    // }
  }

  changeFlag(id) {
    if (document.getElementById(id).src.match("img/ribbon_marked.svg")) {
      document.getElementById(id).src = "img/ribbon.svg"
    } else {
      document.getElementById(id).src = "img/ribbon_marked.svg"
    }
  }

  render() {
    return (
      <div >
        <section class="practice-session quiz-starting-session">
          <div id={this.props.question.id} class="quiz-onscroll ps-ques-wrapper" >
            <span class="ribbon" onClick={() => this.props.flag_response(this.props.question.id, this.state.index)}>
              <img id={this.props.question.id + "-flag"} onClick={() => this.changeFlag(this.props.question.id + "-flag")} src={this.props.question.status.flag == true ? "img/ribbon_marked.svg" : "img/ribbon.svg"} />
            </span>
            <div class="ps-ques-header-wrapper">
              <div class="ps-ques-header-container">
                <div class="ps-ques-header">
                  Single Choice
                </div>
                <div class="ps-ques-count">
                  <span class="ps-current-ques-no">{this.state.index} /</span>
                  <span class="ps-total-ques">{this.props.questions_length}</span>
                </div>
              </div>
              <div class="ps-ques-points-container">
                <div class="ps-ques-points">+{this.state.question.q_positive_mark}</div>
                <div class="ps-ques-points">-{this.state.question.q_negative_mark}</div>
              </div>
            </div>
            <form>
              <div class="ps-ques-block ">
                {parse(this.props.question.q_question[1])}
              </div>
              <div class="ps-ques-choice-section" >
                <label class="ps-choice-container" onClick={() => this.props.response(this.props.question, this.props.question.options[0].id, this.state.index, 0, 1, 2, 3)}>
                  <input id={this.props.question.options[0].id+"-"+this.props.question.id } onClick={() => this.inputClick(this.props.question.options[0].id+"-"+this.props.question.id )} onLoad={this.inputLoad(this.props.question.options[0],this.props.question.options[0].id+"-"+this.props.question.id )} type="radio" name="choice" class="radio_btn" />
                  <span class="option">{parse(this.props.question.options[0].qo_options[1])}</span>
                  <span class="choice"></span>
                </label>
                <label class="ps-choice-container" onClick={() => this.props.response(this.props.question, this.props.question.options[1].id, this.state.index, 1, 0, 2, 3)}>
                  <input id={this.props.question.options[1].id+"-"+this.props.question.id } onClick={() => this.inputClick(this.props.question.options[1].id+"-"+this.props.question.id )} onLoad={this.inputLoad(this.props.question.options[1],this.props.question.options[1].id+"-"+this.props.question.id )} type="radio" name="choice" class="radio_btn" />
                  <span class="option">{parse(this.props.question.options[1].qo_options[1])}</span>
                  <span class="choice"></span>
                </label>
                <label class="ps-choice-container" onClick={() => this.props.response(this.props.question, this.props.question.options[2].id, this.state.index, 2, 0, 1, 3)}>
                  <input id={this.props.question.options[2].id+"-"+this.props.question.id } onClick={() => this.inputClick(this.props.question.options[2].id+"-"+this.props.question.id )} onLoad={this.inputLoad(this.props.question.options[2],this.props.question.options[2].id+"-"+this.props.question.id) } type="radio" name="choice" class="radio_btn" />
                  <span class="option">{parse(this.props.question.options[2].qo_options[1])}</span>
                  <span class="choice"></span>
                </label>
                <label class="ps-choice-container" onClick={() => this.props.response(this.props.question, this.props.question.options[3].id,this.state.index, 3, 0, 1, 2)}>
                  <input id={this.props.question.options[3].id+"-"+this.props.question.id } onClick={() => this.inputClick(this.props.question.options[3].id+"-"+this.props.question.id )} onLoad={this.inputLoad(this.props.question.options[3],this.props.question.options[3].id+"-"+this.props.question.id) } type="radio" name="choice" class="radio_btn" />
                  <span class="option">{parse(this.props.question.options[3].qo_options[1])}</span>
                  <span class="choice"></span>
                </label>
              </div>
            </form>
          </div>
        </section>
      </div>
    );
  }
}
