import React from "react";
import { NavLink } from "react-router-dom"
import Solution from './show_solutions'
const commonservice = require('./services/service')

export default class Submit extends React.Component {
    documentData;
    constructor(props) {
        super(props);
        this.state = {
            _localJson: {},
            _qn_length: 0,
            _quiz_json: "",
            _show_alert: false,
            _right_questions: 0,
            _wrong_questions: 0,
            _unattended: 0,
            _accuracy: '50%',
            _total_mark: 0,
            _total_qn: 0,
            _showSolutions: false,
            _total_time: 0,
            _time_taken: 0

        }
    };
    getdb(data) {
        return new Promise(async (resolve, reject) => {
            try {
                var dataValue = await window.ldb.get(data);
                resolve(dataValue)
            } catch (e) {
                console.log(e);
            }
        })

    }
    setdb = (key, json) => {
        window.ldb.set(key, JSON.stringify(json));

    }
    diff_minutes(dt2, dt1) {
        // var diff = (dt2 - dt1);
        // var delta = Math.abs(dt2 - dt1)/1000;

        var seconds = Math.floor((dt2 - (dt1)) / 1000);
        var minutes = Math.floor(seconds / 60);
        var hours = Math.floor(minutes / 60);
        var days = Math.floor(hours / 24);

        hours = hours - (days * 24);
        minutes = minutes - (days * 24 * 60) - (hours * 60);
        seconds = seconds - (days * 24 * 60 * 60) - (hours * 60 * 60) - (minutes * 60);
        if (minutes < 10) {
            minutes = "0" + minutes
        }
        if (seconds < 10) {
            seconds = "0" + seconds
        }
        return minutes + ":" + seconds
    }
    getTimes() {
        let e = JSON.parse(localStorage.getItem('timeTaken'))
        console.log(e)
        if (e.taken == "00:00") {
            this.setState({ _show_alert: true })
        }
        let h = e.total.split(':')
        let i = e.taken.split(':')
        var dt1 = new Date("October 13, 2014 11:" + h[0] + ":" + h[1]);
        var dt2 = new Date("October 13, 2014 11:" + i[0] + ":" + i[1]);
        // console.log(this.diff_minutes(dt1, dt2));
        this.setState({ _time_taken: e.taken, _total_time: e.total })

        // this.setState({ _time_taken: this.diff_minutes(dt1, dt2), _total_time: e.total })
    }
    async componentDidMount() {
        this.getTimes()
        localStorage.setItem('quiz_status', 'finished')
        localStorage.removeItem('time_left')
        let localJson = JSON.parse(await this.getdb("data"))
        console.log(localJson)
        if (localJson == null) {
            alert('No Data Found')
            this.props.history.push("/")
        } else {
            let sc_mark={ mark: 0, right: 0, wrong: 0 }
            let mc_mark={ mark: 0, right: 0, wrong: 0 }
            let pt_mark={ mark: 0, right: 0, wrong: 0 }
            this.setState({ _localJson: localJson })
            var total_questions = localJson.questions_order.length
            // sc_mark= await commonservice.getSingleChoiceMark(localJson)
            // mc_mark = await commonservice.getMultipleChoiceMark(localJson)
            pt_mark = await commonservice.getPracticeTestMark(localJson)
            let total_mark = sc_mark.mark + mc_mark.mark+pt_mark.mark
            let total_rightQn = sc_mark.right + mc_mark.right+pt_mark.right
            let total_wrongQn = sc_mark.wrong + mc_mark.wrong+pt_mark.wrong
            let unattend = total_questions - (total_rightQn + total_wrongQn)
            this.setState({ _total_qn: total_questions, _total_mark: total_mark.toFixed(2), _right_questions: total_rightQn, _wrong_questions: total_wrongQn, _qn_length: total_questions, _unattended: unattend })
        }
    }

    render() {
        return (
            <div>
                {
                    this.state._show_alert && (
                        <div>
                            <div className="fixed-header">
                            </div>
                            <div className="ps-quiz-sub-popup-block">
                                <div className="swipe-direction-popup" style={{ top: '226px', bottom: 'auto' }}>
                                    <div className="swipe-direction-column">
                                        <div className="swipe-direction-desc">
                                            Alert <br />Your session time out
                                        </div>
                                    </div>
                                    <a onClick={() => this.setState({ _show_alert: false })} className="swipe-btnblk">CLOSE</a>
                                </div>
                            </div>
                        </div>
                    )}
                <section class="test-result-section">
                    <div class="test-result-toppart">
                        <NavLink to="/" >
                            <div class="test-closeicon">&times;</div>
                        </NavLink>

                        <div class="test-result-heading">Result</div>
                    </div>
                    <div class="test-topic-wrapper">
                        <div class="test-topic-container">
                            <div class="test-topic-column">
                                <div class="test-topic-heading">
                                    JEE 2020 Revision: Thermal Properties of Matter Quiz
                                    </div>
                                <div class="test-topic-resp">
                                    <div class="test-topic-questionscount">
                                        {this.state._total_qn} Questions
                                        </div>
                                    <div class="test-total-time">30 Minutes</div>
                                </div>
                            </div>
                            <div class="test-user-avatar-blk">
                                <div class="test-user-avatar">
                                    <img src="img/massey.png" />
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="test-result-report-wrapper">
                        <div class="test-result-report-container">
                            <div class="test-result-score-icon">
                                <img src="img/tick.png" />
                            </div>
                            <div class="test-result-report-caption">Scored</div>
                            <div class="test-result-overview">
                                <div class="test-result-currentvalue">{this.state._total_mark}</div>
                                <div class="test-result-maxvalue">/ {this.state._qn_length}</div>
                            </div>
                        </div>
                        <div class="test-result-report-container">
                            <div class="test-result-score-icon rank-icon">
                                <img src="img/rank.png" />
                            </div>
                            <div class="test-result-report-caption">Rank</div>
                            <div class="test-result-overview">
                                <div class="test-result-currentvalue">1959</div>
                                <div class="test-result-maxvalue">/1965</div>
                            </div>
                        </div>
                        <div class="test-result-report-container">
                            <div class="test-result-score-icon">
                                <img src="img/time.png" />
                            </div>
                            <div class="test-result-report-caption">Time</div>
                            <div class="test-result-overview">
                                <div class="test-result-currentvalue">{this.state._time_taken}</div>
                                <div class="test-result-maxvalue">/{this.state._total_time}</div>
                            </div>
                        </div>
                    </div>
                    <div class="test-evaluation-block">
                        <div class="test-evaluation-container">
                            <div class="d-flex align-items-center mb-20">
                                <div class="test-eval-txt-wrapper">
                                    <div class="test-evaluation-txt">Right Question</div>
                                    <span>:</span>
                                </div>
                                <div class="test-evaluation-value">{this.state._right_questions}</div>
                            </div>
                            <div class="d-flex align-items-center mb-20">
                                <div class="test-eval-txt-wrapper">
                                    <div class="test-evaluation-txt">Unattended</div>
                                    <span>:</span>
                                </div>
                                <div class="test-evaluation-value">{this.state._unattended}</div>
                            </div>
                        </div>
                        <div class="test-evaluation-container">
                            <div class="d-flex align-items-center mb-20">
                                <div class="test-eval-txt-wrapper">
                                    <div class="test-evaluation-txt">Wrong Question</div>
                                    <span>:</span>
                                </div>
                                <div class="test-evaluation-value">{this.state._wrong_questions}</div>
                            </div>
                            <div class="d-flex align-items-center mb-20">
                                <div class="test-eval-txt-wrapper">
                                    <div class="test-evaluation-txt">Accuracy</div>
                                    <span>:</span>
                                </div>
                                <div class="test-evaluation-value">50%</div>
                            </div>
                        </div>
                    </div>
                    <div class="test-result-share-wrapper">
                        <div class="test-result-shareicon">
                            <img src="img/trophy.png" />
                        </div>
                        <div class="test-result-scorecard-caption">
                            You did great!
                            </div>
                        <div class="test-result-desc">
                            Compete with your friends to know where you stand amongst them
                            </div>
                        <div class="test-result-share-btnblk">
                            <div class="tr-result-share-icon">
                                <img src="img/share-icon.svg" />
                            </div>
                            <div class="tr-result-btntxt">
                                Share Scorecard
                                </div>
                        </div>
                    </div>
                    <div
                        onClick={() => { this.setState({ _showSolutions: !this.state._showSolutions }) }}
                        class="test-show-solutions-btnblock" id="show_solutions_acn">
                        {this.state._showSolutions == false ? 'SHOW SOLUTIONS' : 'HIDE SOLUTIONS'}
                    </div>
                    {(this.state._showSolutions &&
                        <div class="test-show-solution-block">
                            <Solution _localJson={this.state._localJson} />
                        </div>
                    )}
                </section>

            </div>
        )
    }

}