const getdb = (key) => {
    return new Promise(async (resolve, reject) => {
        try {
            var dataValue = await window.ldb.get(key);
            resolve(dataValue)
        } catch (e) {
            console.log(e);
        }
    })

}
const setdb = (key, value) => {
    window.ldb.set(key, JSON.stringify(value));

}
const check_isAllCorrect_multi_choice = (options, correct_option_id, pve, nve) => {
    return new Promise((resolve, reject) => {
        try {
            // let status = false
            let match_id = []
            let total_ids = []
            for (let i in options) {
                if (options[i].option_id != undefined && options[i].option_id != '') { total_ids.push(options[i].option_id) }
                if (options[i].option_id == options[i].correct_option_id && options[i].option_id != undefined) {
                    match_id.push(match_id + options[i].option_id)
                }
                if ((correct_option_id.split(',').length === match_id.length) && (match_id.length === total_ids.length)) {
                    resolve({ status: true, positive: pve, negative: nve })
                }
            }
            resolve({ status: false, positive: pve, negative: nve })
        } catch (e) {
            resolve({ status: false, positive: pve, negative: nve })
        }
    })
}
export const generateAnswer = (shuffledData) => {
    return new Promise((resolve, reject) => {
        try {
            let question_order = shuffledData.questions_order
            let arranged_questions = []
            question_order.map((element, index) => {
                var qn_obj = {
                    id: shuffledData.questions[element].id,
                    q_type: shuffledData.questions[element].q_type,
                    q_question: shuffledData.questions[element].q_question,
                    q_explanation: shuffledData.questions[element].q_explanation,
                    q_answer: shuffledData.questions[element].q_answer
                }
                if (!shuffledData.questions[element].status) {
                    qn_obj["status"] = { qn: "notvisited", flag: false }
                } else {
                    qn_obj["status"] = shuffledData.questions[element].status
                }
                let optn = []
                shuffledData.questions[element].options.map((item) => {
                    if (typeof item.id === 'number') { item.id = item.id.toString() }
                    if (shuffledData.questions[element].q_answer.split(',').includes(item.id)) {
                        item['ans'] = true
                        optn.push(item)
                    } else {
                        item['ans'] = false
                        optn.push(item)
                    }

                })
                let wholeAns = 0
                optn.map((optn, i) => {
                    if (optn['class'] == 'ques-choice-marked' && optn.ans == true) {
                        wholeAns = wholeAns
                    } else if (optn['class'] == 'ques-choice-marked' && optn.ans == false) {
                        wholeAns = wholeAns + 1
                    } else if (!optn['class'] && optn.ans == true) {
                        wholeAns = wholeAns + 1
                    }
                })
                if (wholeAns == 0) {
                    qn_obj['whole_ans'] = true
                } else {
                    qn_obj['whole_ans'] = false
                }
                // console.log(wholeAns)
                qn_obj.options = optn
                arranged_questions.push(qn_obj)
            });
            resolve(arranged_questions)
        } catch (e) {
            console.log(e)
            reject()
        }
    })
}
export const generatePracticeTestAnswer = (questions) => {
    let question = []
    questions.map((qn) => {
        if (qn.q_type == 1) {
            // console.log(qn)
            let options = []
            qn.options.map((optn) => {
                if (optn.id == qn.q_answer) {
                    optn['is_correct'] = true
                    options.push(optn)
                } else {
                    optn['is_correct'] = false
                    options.push(optn)
                }
            })
            qn['options'] = options
            question.push(qn)

        } else {
            return []
        }
    })
    return question
}
export const getPracticeTestMark = (localJson) => {
    return new Promise(async (resolve, reject) => {
        let mark = 0;
        let localJsonArray = []
        let rightQn = 0
        let wrongQn = 0
        try {
            for (let val in localJson.PT_attended) {
                localJsonArray.push(localJson.PT_attended[val])
            }
            localJsonArray.forEach((item) => {
                if (item.correct_answer == item.option_id) {
                    mark = mark + parseInt(item.positive_mark)
                    rightQn = rightQn + 1
                } else {
                    mark = mark - item.negative_mark
                    wrongQn = wrongQn + 1
                }
            })
            console.log(mark)

            resolve({ mark: mark, right: rightQn, wrong: wrongQn })
        } catch (e) {
            resolve({ mark: 0, right: 0, wrong: 0 })
        }

    })
}
